/* https://paletton.com/#uid=1000u0k8X1S39dA2h4rf-1QlS1V General Coloring */

.App {
  text-align: center;
  display: flex;
  flex-direction: row;
  width: 100%;
  color: white !important;
}
.App_mobile {
  text-align: center;
  display: flex;
  flex-direction: row;
  width: 100%;
  color: white !important;
  height: 100vh;
  overflow: hidden;
}
.mid {
  width: inherit;
  background-color: #232121;
  padding-bottom: 2%;
  font-family: Montserrat;
}
.light-screen {
  background: cornflowerblue !important;
  color: white;
}
.light-top-component {
  background: white !important;
  color: black;
  border-bottom: black solid 1px;
}
.light-component {
  background: white !important;
  color: black;
  border: black solid 1px;
}
.midpart {
  width: inherit;
  background-color: #232121;
  padding-bottom: 2%;
  font-family: "Montserrat";
}

.screen {
  width: inherit;
  background-color: #232121;
  font-family: "Montserrat";
}
.pro_pic {
  height: 55vh;
  border-radius: 50%;
}
.pro_pic_mobile {
  height: 25vh;
  border-radius: 200px;
  min-height: 125px;
  margin: 12%;
}
.welcome_text {
  font-size: 60px;
}
.expertise {
  color: black;
}
.route {
  margin: 5%;
  background: dimgray;
  border: 1px solid black;
  padding: 1%;
  border-radius: 10px;
}
.resume_holder {
  margin-left: 14vw;
  width: 50vw;
  overflow: hidden;
  border: 1px black solid;
  box-shadow: 5px 5px 15px 5px black;
}
.resume_holder_mobile {
  width: 90vw;
  margin-left: 5%;
}
.logo-holder-mobile {
  display: flex;
  flex-direction: row;
  margin-left: 15%;
}
.react-pdf__Page__canvas {
  height: auto !important;
  width: inherit !important;
}
.react-pdf__Page {
  width: inherit;
  overflow: hidden;
}
